"use server";

import axios from "axios";

type ActionResponse = {
    message:string,
    errors:Array<string>
}

export const registerAction = async ( prev:ActionResponse, formData:FormData ):Promise<ActionResponse>=>{




    try{
        const res = await axios.post(
            `${process.env.BACKEND_API_URL}/user/register`, 
            { name: formData.get("username"), email: formData.get("email"), password: formData.get("password"), confirm_password: formData.get("confirm-password") }, 
            { headers:{ "Content-Type":"application/json"}}
        );
        console.log(res.data);
        console.log(res.data.response);
    }
    catch(err:any){
        console.log("--- ",err);
        console.log(err.errors)
        console.log(err.response);
    }

    return{
        message:"",
        errors:[]
    }



} 





















