"use client";

import { registerAction } from '@/actions/authActions';
import Link from 'next/link';
import React, { useActionState } from 'react'
import { useFormState } from 'react-dom';

function RegisterForm(){

    const initialState = {
        message:"",
        errors:[]
    }

    const [ state, formAction]  = useFormState( registerAction, initialState);

    console.log("state.... ",state);
   
    return(
        <div>
        <form
          noValidate
          className="bg-white p-8 rounded shadow-md w-96"
          action={formAction}
        >

          <h1 className="text-2xl text-pink-500 font-bold text-center mb-6">Register</h1>
          
          <div className="mb-4">
            <label htmlFor="username" className="block text-sm font-medium text-gray-700">Username</label>
            <input type="text" name='username' id="username" className="mt-1 block w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring focus:ring-blue-500" required />
          </div>

          <div className="mb-4">
            <label htmlFor="email" className="block text-sm font-medium text-gray-700">Email</label>
            <input type="email" name='email' id="email" className="mt-1 block w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring focus:ring-blue-500" required />
          </div>

          <div className="mb-4">
            <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
            <input type="password" name="password" id="password" className="mt-1 block w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring focus:ring-blue-500" required />
          </div>

          <div className="mb-4">
            <label htmlFor="confirm-password" className="block text-sm font-medium text-gray-700">Confirm Password</label>
            <input type="password" name="confirm-password" id="confirm-password" className="mt-1 block w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring focus:ring-blue-500" required />
          </div>

          <button type="submit" className="w-full bg-pink-500 text-white p-2 rounded hover:bg-pink-600 transition duration-200">Create Account</button>

          <p className="mt-4 text-center">
            Already Have an account? <Link href="/login" className="text-blue-500 hover:underline">Login</Link>
          </p>
        </form>
    </div>
    )
}

export { RegisterForm};